import axios from 'axios';
import { Notification } from 'element-ui';
import config from './../config/index';

/**
 *   axios 
 * @type {AxiosInstance}
 */
const service = axios.create({
  timeout: 30000, // 
  baseURL: config.baseApi,
});

// 
service.interceptors.request.use((config) => {
  const headers = config.headers;
  // TODO token
  if (!headers.authToken) headers.authToken = 'token';
  return config;
});

// 
service.interceptors.response.use(
  (response) => {
    // 
    // TODO code, data, msg 
    // TODO success errorCode errorMessage
    const { code, data, msg } = response.data;
    // code0data
    if (code === 0) {
      return data;
    } else {
      Notification.error(msg || '!');
      return Promise.reject(msg || '!');
    }
  },
  (err) => {
    Notification.error('!');
    return Promise.reject(err);
  },
);

/**
 * 
 * @param {url} 
 * @param {params} 
 * @param {options} 
 * @param mock mock
 */
function request(options) {
  // options.methodget
  options.method = options.method || 'get';
  // getparams
  if (options.method.toLowerCase() === 'get') {
    !options.params && (options.params = options.data);
  }
  // mock
  if (config.env === 'prod') {
    service.defaults.baseURL = config.baseApi;
  } else {
    let isMock = false;
    if (Object.prototype.hasOwnProperty.call(options, 'mock')) {
      isMock = !!options.mock;
    } else {
      isMock = !!config.mock;
    }
    console.log('isMock:', isMock);
    service.defaults.baseURL = isMock ? config.mockApi : config.baseApi;
  }
  // 
  return service(options);
}

// this.$request.get()
['get', 'post', 'patch', 'delete', 'put'].forEach((item) => {
  request[item] = (url, data, options) => {
    return request({
      url,
      data,
      method: item,
      ...options,
    });
  };
});
export default request;
